<template>
  <div>
    <headLeft></headLeft>
    <div class="right-content" v-if="load">
      <div class="u-right" style="min-height:300px">
        <div class="big-title">关键字</div>
        <div class="bottom-setting">
          <div class="key-box">
            <el-tag  color="#5488F9" v-for="keyword in temporaryValue" :key="keyword" v-if="temporaryValue.length>0"><p :title="keyword">{{ keyword }}</p></el-tag>
          </div>
          <div class="left-btn" >
            <el-button type="default" class="center-btn" @click="popIsshow = true">设置关键字</el-button>
          </div>
        </div>
      </div>
    </div>
    <el-dialog title="设置关键字" v-model="popIsshow" custom-class="edit-keyword">
      <div class="desc">
        （如：工作职位，行业，所在地等），我们会根据关键词智能为你推荐社交平台中的联系人。(关键词最多可设置20个)
      </div>
      <div class="key-box">
        <el-tag
          :key="tag"
          v-for="tag in dynamicTags"
          :closable="true"
          :close-transition="false"
          @close="handleClose(tag)"
          color="#5488F9"
        v-if="dynamicTags.length>0">
          <p :title="tag">{{tag}}</p>
        </el-tag>
        <el-input
          class="input-new-tag"
          v-if="inputVisible"
          v-model="inputValue"
          ref="saveTagInput"
          size="mini"
          @keyup.enter.native="handleInputConfirm"
          @blur="handleInputConfirm"
        >
        </el-input>
        <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
      </div>
      <div class="prompt keyword-prompt"><i class="icon-prompt"></i><span>关键字生效时间为24小时后</span></div>
      <div slot="footer" class="dialog-footer" :class="{'is-ok': dynamicTags.length>0}">
        <el-button type="primary" @click="updateKeywordFunc()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import systemSettingAPI from '@/api/systemSettingAPI'
import headLeft from './systemIndex'
export default {
  name: 'setKeyWord',
  data () {
    return {
      popIsshow: false,
      dynamicTags: [],
      inputVisible: false,
      inputValue: '',
      temporaryValue: [],      // 关键字展示
      load: false
    }
  },
  components: {
    headLeft
  },
  watch: {
    dynamicTags: function () {
      console.log('dynamicTags:' + this.dynamicTags)
    },
    temporaryValue: function () {
      console.log('temporaryValue:' + this.temporaryValue)
    }
  },
  methods: {
    handleClose (tag) {
      this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1)
    },
    showInput () {
      this.inputVisible = true
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // 关键字更新
    updateKeywordFunc () {
      if (this.dynamicTags !== '' && this.dynamicTags !== null && this.dynamicTags !== undefined) {
        let keywords = this.dynamicTags.join(';')
        if (keywords.length === 0) {
          return
        }
        if (keywords.length < 600) {
          let param = {keywords: keywords}
          systemSettingAPI.updateKeyword(param).then(res => {
            if (res.data.code === 0) {
              this.$message({
                message: '关键字保存成功',
                type: 'success'
              })
              this.temporaryValue = this.dynamicTags // 成功后暂存数据赋值给标签展示
              this.popIsshow = false
            } else {
              this.$message.error(res.data.message)
            }
          })
        } else {
          this.$message.error('关键字长度过长或超出限制')
        }
      } else {
        this.$message.error('关键字不能为空')
      }
    },
    // 获取关键字
    handleInputConfirm () {
      let inputValue = this.inputValue
      if (inputValue !== '' && inputValue !== null && inputValue !== undefined) {
        this.dynamicTags.push(inputValue)      // 设置后
      }
      this.inputVisible = false
      this.inputValue = ''
    }
  },
  created () {
    // 智能关键字获取
    systemSettingAPI.getKeywords().then(res => {
      if (res.data.code === 0) {
        if (res.data.data !== null) {
          let keywords = res.data.data.keywords
          if (keywords !== '' && keywords !== null && keywords !== undefined) {
            this.dynamicTags = keywords.split(';')
            this.temporaryValue = keywords.split(';')  // 初始展示和设置一致
          }
        }
        this.load = true
      }
    })
  }
}
</script>
<style lang="less" rel="stylesheet/less">
  @import '../../assets/style/base.less';
  .prompt{
    font-size: 12px;
    color: #FFB822;
    line-height: 16px;
    span{
      margin-left:5px;
    }
  }
  .keyword-prompt{
    margin-top:4px;
  }
  .right-content{
    .big-title{
      font-size:16px;
      line-height: 22px;
      padding:0 25px;
    }
    .bottom-setting{
      margin-top:15px;
      padding:0 25px;
      overflow:hidden;
      .key-box{
        width:79%;
        float:left;
        border-right: 1px solid #EAF0F6;
        min-height: 200px;
        overflow:hidden;
        .el-tag{
          margin-right:8px;
          margin: 5px;
          p{
            max-width: 180px;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
          }
        }
      }
      .left-btn{
        text-align: center;
        width:20%;
        float:left;
        .center-btn{
          width:100px;
          color:#fff;
          background: #5488F9;
          border-radius: 4px;
          padding:8px 0;
          font-size:13px;
          line-height: 18px;
          display: inline-block;
          cursor: pointer;
          margin-right: -25px;
        }
        .center-btn:hover{
          opacity: 0.7;
        }
      }
    }
  }
  /*设置关键字弹框*/
  .edit-keyword{
      width:390px;
      .desc{
        font-size: 12px;
        color: #7C98B6;
      }
      .key-box{
        margin-top:8px;
        border: 1px solid #CBD6E3;
        border-radius: 4px;
        padding:4px 4px;
        height:140px;
        overflow: auto;
        .el-input{
          margin-top:4px;
        }
        .el-tag{
          margin-left:4px;
          margin-top:4px;
          p{
            max-width: 180px;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            display:inline-block;
          }
          i{
            margin-bottom:10px;
          }
        }
      }
    .el-dialog__body{
      padding:16px 20px;
    }
    .el-dialog__footer{
      padding: 0px 17px 15px;
      .dialog-footer{
        .el-button{
          width:100%;
          background:#EAF0F6;
          border: 1px solid #DFE3EB;
          color: #B0C1D4;
        }
      }
      .is-ok{
        .el-button{
          background: #5488F9;
          border:none;
          color: #fff;
        }
        .el-button:hover{
          opacity: 0.7;
        }
      }
    }
    .button-new-tag{
      vertical-align: top;
      margin-top: 3px;
    }
  }

  .is-ok{
    .el-button{
      background: #5488F9;
      border:none;
      color: #fff;
    }
    .el-button:hover{
      opacity: 0.7;
    }
  }
</style>




